<template>
	<div class="home">
		<!-- 一级分类标题 -->
		<div class="type-one" v-if="typeOneList.length">
			<router-link class="type" v-for="(type, index) in typeOneList" :key="index" :to="'/typelist/' + type" @click.native="$store.commit('SET_TYPE_ONE_INDEX', index)" :class="{ active: typeOneIndex == index }">
				{{ type }}
			</router-link>
		</div>

		<router-view></router-view>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
import { getTypeOneApi } from '@/api/goods';

export default {
	name: 'home',
	data() {
		return {
			typeOneList: [],
		};
	},
	computed: {
		...mapGetters(['typeOneIndex']),
	},
	created() {
		this.getTypeOne();
	},
	methods: {
		async getTypeOne() {
			let res = await getTypeOneApi();
			this.typeOneList = res;
			this.$store.commit('SET_TYPE_ONE_LIST', res);
		},
	},
};
</script>
<style lang="scss" scoped>
.type-one {
	margin: 20px 0;
	padding: 10px 0;
	display: flex;
	flex-wrap: wrap;
	box-shadow: 0 0 2px #ccc;
	.type {
		flex-shrink: 0;
		padding: 5px 10px;
		border-bottom: 2px solid transparent;
		margin: 0 10px;
		text-align: center;
		border-radius: 20px;
	}
	.type:hover,
	.type.active {
		color: #fff;
		background-color: #800080;
	}
}
</style>
